<template>
  <div class="image_list">
    <el-card class="box-card" shadow="hover">
      <el-radio-group v-model="college" @change="loadImages(1)" size="mini">
        <el-radio-button label="false">全部</el-radio-button>
        <el-radio-button label="true">收藏</el-radio-button>
      </el-radio-group>
      <el-button
        style="float: right"
        @click="dialogTableVisible = true"
        type="success"
        size="mini"
        v-if="isShowAdd"
        >上传素材</el-button
      >
      <!-- 素材列表 -->
      <el-row :gutter="20">
        <el-col
          :xs="24"
          :sm="12"
          :md="8"
          :lg="6"
          :xl="4"
          v-for="(img, index) in images"
          :key="index"
          @click.native="selected = index"
          class="list_img_item"
        >
          <el-image class="list_images" :src="img.url" fit="cover"> </el-image>
          <div class="list_img_footer" v-if="isShowAdd">
            <el-button
              type="warning"
              :icon="img.is_collected ? 'el-icon-star-on' : 'el-icon-star-off'"
              @click="onCollect(img)"
              circle
              :loading="img.loading"
              size="mini"
            >
            </el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              circle
              :loading="img.loading"
              @click="onDelete(img)"
              size="mini"
            >
            </el-button>
          </div>
          <div
            class="list_img_selected"
            v-if="isShowSelected && selected === index"
          ></div>
        </el-col>
      </el-row>
      <el-pagination
        background
        @current-change="onPageChange"
        layout="prev, pager, next"
        :current-page.sync="page"
        :page-size="pageSize"
        :total="totalCount"
      >
      </el-pagination>
    </el-card>
    <el-dialog
      title="图片上传"
      :visible.sync="dialogTableVisible"
      :close-on-click-modal="false"
      width="19vw"
      :append-to-body="true"
    >
      <el-upload
        :show-file-list="false"
        class="upload-demo"
        drag
        :headers="uploadHeaders"
        name="image"
        :on-success="onUploadSuccess"
        action="http://api-toutiao-web.itheima.net/mp/v1_0/user/images"
        multiple
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">
          只能上传jpg/png文件，且不超过500kb
        </div>
      </el-upload>
    </el-dialog>
  </div>
</template>

<script>
import { getImages, collectImage, deleteImage } from '@/api/image'
export default {
  name: 'ImageList',
  props: {
    isShowAdd: {
      type: Boolean,
      default: true
    },
    isShowSelected: {
      type: Boolean,
      default: false
    }
  },
  data () {
    const user = JSON.parse(window.localStorage.getItem('user'))
    return {
      college: false,
      images: [],
      dialogTableVisible: false,
      uploadHeaders: {
        Authorization: `Bearer ${user.token}`
      },
      totalCount: 0,
      pageSize: 20,
      page: 1,
      selected: null
    }
  },
  created () {
    this.loadImages(1, false)
  },
  methods: {
    loadImages (page = 1) {
      this.page = page
      getImages({
        collect: this.college,
        page,
        per_page: this.pageSize
      }).then((res) => {
        const results = res.data.data.results
        results.forEach((img) => {
          img.loading = false
        })
        this.images = results
        this.totalCount = res.data.data.total_count
      })
    },
    onUploadSuccess () {
      this.dialogTableVisible = false
      this.loadImages(this.page)
      this.$message({
        type: 'success',
        message: '上传成功'
      })
    },
    onPageChange (page) {
      this.loadImages(page)
    },
    onCollect (img) {
      img.loading = true
      collectImage(img.id, !img.is_collected).then((res) => {
        img.is_collected = !img.is_collected
        img.loading = false
      })
    },
    onDelete (img) {
      img.loading = true
      deleteImage(img.id).then((res) => {
        this.loadImages(this.page)
        img.loading = false
      })
    }
  }
}
</script>

<style scoped lang="less">
.list_img_item {
  position: relative;
  .list_images {
    width: 240px;
    height: 240px;
    -webkit-user-drag: none;
    margin-top: 30px;
  }
  .list_img_selected {
    width: 80px;
    height: 80px;
    background: url(./selected.png) no-repeat;
    background-size: 100% 100%;
    position: absolute;
    top: 80%;
    left: 45%;
    margin-left: -40px;
    margin-top: -40px;
  }
  .list_img_footer {
    height: 30px;
    width: 240px;
    cursor: pointer;
    position: absolute;
    bottom: 16px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color: rgb(255, 255, 255);
  }
}
</style>
